<template>
  <!-- Portfolio Section -->
  <section class="page-section">
    <b-container>
      <HeaderPage title="Adicionar Animal" />

      <!--FORM-->
      <b-row>
        <b-col cols="2"></b-col>
        <b-col>
          <form @submit.prevent="add">
            <div class="form-group">
              <input
                v-model="name"
                type="text"
                class="form-control form-control-lg"
                id="txtName"
                placeholder="escreve nome"
                required
              />
            </div>
            <div class="form-group">
              <select id="sltGroup" class="form-control form-control-lg" v-model="group" required>
                <option value>-- SELECIONA GRUPO --</option>
                <option value="anfibio">ANFÍBIO</option>
                <option value="ave">AVE</option>
                <option value="mamífero">MAMÍFERO</option>
                <option value="peixe">PEIXE</option>
                <option value="reptil">RÉPTIL</option>
              </select>
            </div>
            <div class="form-group">
              <textarea
                id="txtDescription"
                class="form-control form-control-lg"
                placeholder="escreve descrição"
                cols="30"
                rows="10"
                v-model="description"
                required
              ></textarea>
            </div>
            <div class="form-group">
              <div class="form-group">
                <input
                  v-model="level"
                  type="text"
                   onmouseenter="(this.type='number')"
                onmouseleave="(this.type='text')"
                  min="1"
                  max="5"
                  class="form-control form-control-lg"
                  id="txtLevel"
                  placeholder="escreve o nível"
                  required
                />
              </div>
            </div>
            <!--Links-->
            <div v-if="links.length">
              <div class="row">
                <div class="col-md-9">
                  <h4>Lista de links</h4>
                </div>
                <div class="col-md-3">
                  <h4>Ações</h4>
                </div>
              </div>
            </div>
            <div v-for="(link, index) in links" :key="link._id">
              <div class="form-group">
                <div class="row">
                   <div class="col-md-6">
                    <input
                      v-model.lazy="link.url"
                      type="url"
                      class="form-control"
                      id='txtUrl'
                      :placeholder="setPlaceHolder(index+1)"
                      
                    />
                  </div>
                  <div class="col-md-3">
                    <select
                      v-model="link.types"                      
                      class="form-control"
                      required
                    >
                      <option value="">-- TIPO --</option>
                      <option value="photo">IMAGEM</option>
                      <option value="video">VÍDEO</option>
                      <option value="sound">ÁUDIO</option>
                    </select>
                  </div>
                  <div class="col-md-3">
                    <button
                      @click="removeLink(index)"
                      type="button"
                      class="btn btn-outline-danger mr-2"
                    ><i class="fas fa-trash"></i> REMOVER</button>
                  </div>
                </div>
              </div>
            </div>
<button
              @click="addLink"
              type="button"
              class="btn btn-outline-success btn-lg mr-2"
            ><i class="fas fa-plus-square"></i> ADICIONAR LINKS</button>
            <!--<pre>{{ $data }}</pre>-->

            <button type="submit" class="btn btn-outline-success btn-lg mr-2">
              <i class="fas fa-plus-square"></i>  ADICIONAR</button>
            <router-link
              :to="{name: 'listAnimals'}"
              tag="button"
              class="btn btn-outline-danger btn-lg"
            ><i class="fas fa-window-close"></i>  CANCELAR</router-link>
          </form>
        </b-col>
        <b-col cols="2"></b-col>
      </b-row>
    </b-container>
  </section>
</template>

<script>
import { MODULE_NAME, ADD_ANIMAL } from "@/store/animals/animal.constants";
import HeaderPage from "@/components/HeaderPage.vue";
import router from "@/router";
import { mapGetters } from "vuex";

export default {
  name: "AddAnimal",
  components: {
    HeaderPage
  },
  data: () => {
    return {
      name: "",
      group: "",
      description: "",
      level: "",
      links: [],
      evaluation: [],
      comments: []
    };
  },
  computed: {
    ...mapGetters("animal", ["getMessage"])
  },
  methods: {
    setPlaceHolder: index => {
      return `insira a ${index}º link`;
    },
    addLink() {
      this.links.push({ url: "", types:"" });
    },
    removeLink(index) {
      this.links.splice(index, 1);
    },
    add() {
      this.$store
      .dispatch(`${MODULE_NAME}/${ADD_ANIMAL}`, this.$data)
      .then(() => {
          this.$alert(this.getMessage, "Animal adicionado!", "success");
          router.push({ name: "listAnimals" });
        })
        .catch(err => this.$alert(`${err.message}`, "Erro", "error"));
    }
  }
};
</script>
